import React from 'react';
 import {  Link } from 'react-router-dom';
 

const Nav =()=>{
     return (
            <div>
              
                <ul className="nav-ul">
                <li><Link to="/">Vehicles</Link></li>
                <li><Link to="/add">Add Vehicles</Link></li>
                <li><Link to="/map">Go to the Map</Link></li>
                
                
               </ul>
               
               

            </div>
        )
}

export default Nav;